/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-create-block-kodigo-block {
  border: 1px dotted #f00;
}

.kodigo-markup {
  display: inline-block;
  position: relative;
  border-bottom: solid 1px blue;
  color: blue;
  cursor: pointer;
}

.kodigo-markup .kodigo-control {
  position: absolute;
  top: -5px;
  right: 0;
  background-color: transparent;
  font-size: 12px;
}

.kodigo-form-control {
  width: 100%;
  max-width: 100% !important;
}

.kodigo-form-wrapper {
  margin-bottom: 20px;
}

.kodigo-form-wrapper label {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

.kodigo__ui-dialog .components-button {
  height: auto;
  padding: 6px 10px;
}

.kodigo__ui-dialog .kodigo-image--wrapper {
  position: relative;
}

.kodigo__ui-dialog .kodigo-image--wrapper button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
}